* {
  margin: 0;
  padding: 0;
}

.main-home-container {
  font-family: "Inclusive Sans", sans-serif;
}

html {
  scroll-behavior: smooth;
}

.main {
  color: #c2c2c2;
  background: -webkit-linear-gradient(-45deg, #f4f4f4 30%, #8b8b8b);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  
}

.title-main {
  font-family: Brittany;
}

.bg {
  width: 100vw;
  height: 37rem;
  margin-top: 4.5rem;
  position: absolute;
  top: 0;
  right: 0;
  z-index: 0;
  background: radial-gradient( #00000043 15%, #00000045 ),url(/src/Components/images/third.jpg);
  background-position: center;
  background-size: cover;
  
}

.sponsor-container h2 {
  background: -webkit-linear-gradient(-25deg,rgb(0, 128, 255),rgb(255, 0, 21));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}


@media (min-width: 768px) {
  .bg {
    height: 37rem;
  }
}

.container {
  font-family: sans-serif;
  z-index: 1;
}

.title-main {
  color: #c2c2c2;
  /* background: -webkit-linear-gradient(-45deg, #81c2ff 20%, #7c0000); */
  background: -webkit-linear-gradient(-25deg,rgb(114, 185, 255),rgb(240, 106, 106));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
   z-index: 1;
}
